import { QueryParam } from "@utils/QueryParam";
import { action, makeObservable, observable, computed } from "mobx";
import QueryString, { ParsedQs } from "qs";

type PrivateFields = "_params";
export type ParamsType =
  | undefined
  | string
  | string[]
  | QueryString.ParsedQs
  | QueryString.ParsedQs[];

export default class QueryParamsStore {
  private _params: ParsedQs = {};
  private _search: string = "";

  constructor() {
    makeObservable<QueryParamsStore, PrivateFields>(this, {
      _params: observable.ref,
      params: computed,
      vsCurrency: computed,
      category: computed,
      page: computed,
      name: computed,
      per_page: computed,
      scroll: computed,
      sort: computed,
      setSearch: action,
    });
  }

  get params() {
    return this._params;
  }

  get vsCurrency(): string {
    return this.getStringifiedParam(QueryParam.CURRENCY) || "usd";
  }

  get category(): string {
    return this.getStringifiedParam(QueryParam.CATEGORY);
  }

  get page(): string {
    return this.getStringifiedParam(QueryParam.PAGE);
  }

  get name(): string {
    return this.getStringifiedParam(QueryParam.NAME);
  }

  get per_page(): string {
    return this.getStringifiedParam(QueryParam.PER_PAGE);
  }

  get scroll(): string {
    return this.getStringifiedParam(QueryParam.SCROLL);
  }

  get sort(): string {
    return this.getStringifiedParam(QueryParam.SORT);
  }

  getStringifiedParam(key: QueryParam): string {
    const p = this.getParam(key);
    if (p !== null && typeof p !== "undefined" && p !== "") {
      return `${p}`;
    }
    return "";
  }

  getParam(key: string): ParamsType {
    return this._params[key];
  }

  setSearch(search: string) {
    search = search.startsWith("?") ? search.slice(1) : search;
    if (this._search !== search) {
      this._search = search;
      this._params = QueryString.parse(search);
    }
  }
}
